				"html": "<p>我々の持っている自然に対する畏敬の念は<br>\n自然を完璧には理解できないし<br>\n制御することもできない<br>\n我々はその中で生かされている<br>\nという現実からくるものではないだろうか。  </p>\n<p>もし，惑星や宇宙を自由に創造したり変容させたり<br>\nできるようなテクノロジーを持った知性からすれば<br>\n我々が認識している自然というものに対して<br>\n畏敬の念を持つことはないのではないだろうか。  </p>\n<p>逆に言えば，自然に対して畏敬の念を持たない者は<br>\n人類は（自分は）何でもできる，何をしてもよい<br>\nという傲りや勘違いではないか。<br>\nあるいは，無知によるものか。  </p>\n<p>自然への畏敬の念や道徳的観念は<br>\n人類の歴史的英知であり，伝承されてきたものだ。  </p>\n<p>科学は爆発的に発展してきたが現代では万能感が独り歩きしている。<br>\n何かが科学的に立証されたと言われれば納得する人も多いだろう。<br>\nかつて宗教が担っていた役割の一部が科学に置き換わってしまったようだ。  </p>\n<p>都市部に多く人が住むようになったことも原因の一つと考えられる。<br>\n都市は人間が考えたものを具現化したものだ。<br>\nある種，世界を思い通りに変容させた結果である。<br>\n自然から遠く，人工物（科学）に近い。<br>\n公園の緑ですら，ほとんどの場合，人工的なものだ。  </p>\n<p>農業に従事している人などは<br>\n自然のように思い通りにならないことがあるのは<br>\n当然のことであると受け入れている。  </p>\n<p>都市で生きるものは<br>\n何でも思い通りになるのが当たり前だと思っているので<br>\n思い通りにならないことがあると苛立ち，悩む。  </p>\n<p>我々はより多くを知る必要があるし<br>\n同時により謙虚になる必要がある。  </p>\n<p>いまここにあることができるのは<br>\n過去からの歴史が紡がれてきた結果であり<br>\n未来へとその可能性を残す責任がある。  </p>\n<p>そのために，いま何をするべきか。  </p>",
